<template>
    <div class="releasePost">
          <van-nav-bar
            title="发布帖子"
            left-text="返回"
            @click-left="back"
            left-arrow
        />
        <div class="releasePost_box">
            <div class="box_item">
                 <div class="box_title">发布标题</div>
                <textarea class="userOpenText" placeholder="分享快乐(标题不可超过20字):" ref="inptitle"></textarea>
            </div>
            <div class="box_item">
                 <div class="box_title">发布内容</div>
                <textarea class="userOpenContent" placeholder="发布精彩内容:" ref="inpContent"></textarea>
            </div>
        </div>
        <div class="butPost">
            <van-button type="primary" block round @click="onSumbit">立即发布</van-button>
        </div>
    </div>
</template>

<script>
import {createNamespacedHelpers} from 'vuex'

const {mapState,mapMutations} = createNamespacedHelpers('homeModules')

    export default {
        data(){
            return {
                o: {
                    typeData: 'myPostData',
                    title: '',
                    userNick: '',
                    userHeadPortrait: '',
                    userContent: '',
                    CollectionNumber: 0,
                    uerIsCollection: true,
                    commentNumberContent: [
                    ],
                    openPostDate: {
                        date1: '',
                        date2: ''
                    }
                }
            }
        },
        created(){
            this.getMyInfoData();
            if(localStorage.getItem('__Tk')==null){
                this.$toast('请先登录.....');
                this.$router.push({name: "Login"});
            }
        },
        methods:{
            ...mapMutations(['addPostContentData']),
             getMyInfoData(){
                let tokenString = localStorage.getItem('__Tk');
                let date1=new Date();
                let y=date1.getFullYear();
                let m=date1.getMonth()+1;
                let d=date1.getDate();
                let date=y+"-"+m+"-"+d;
                let sumData=date1.getHours()+":"+date1.getMinutes();
               
                this.o.openPostDate={
                    date1: date,
                    date2: sumData
                }

                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })

                this.axios({
                    method: "GET",
                    url: 'http://www.kangliuyong.com:10002/findMy',
                    params:{
                       appkey:  'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=',
                       tokenString,
                    }
                }).then(result=>{
                    let res=result.data.result;
                    this.$toast.clear();
                    if(result.data.code=="A001"){
                        // this.userInfo=result.data.result[0];
                        this.o.userNick=res[0].nickName;
                        this.o.userHeadPortrait=res[0].userImg;
                    }
                    
                    
                }).catch(err=>{
                    this.$toast.clear();
                    
                })
            },
            onSumbit(){
                let inpTitle=this.$refs.inptitle.value;
                let inpContent=this.$refs.inpContent.value;
                if(inpTitle==""||inpContent==""){
                     this.$toast.fail("请填写完内容...");
                     return;
                }
                if(inpTitle.length>20){
                     this.$toast.fail("标题不可超过20字");
                     return;
                }
                let mydate=new Date();
                this.o.title=inpTitle;
                this.o.userContent=inpContent;
                this.o.id=mydate.getFullYear()+(mydate.getMonth() + 1)+mydate.getDate()+mydate.getHours()+mydate.getMinutes()+mydate.getSeconds();
                

                this.addPostContentData({type: 'myPostData',objContent: this.o});
                this.o.typeData='newPostData';
                // this.o.typeData1='newAndMyPostData';
                this.addPostContentData({type: 'newPostData',objContent: this.o});
                this.$router.push({name: 'ClassIfiCation'})

            
            },
            back(){
                this.$router.go(-1);
            }
        }
    }
</script>

<style lang="less" scoped>
     .releasePost_box{
         padding: 20px;
         .box_title{
            //  text-align: center;
            font-size: 14px;
            margin-bottom: 10px;
         }
         .box_content{
              font-size: 14px;
         }
         .userOpenText,.userOpenContent{
            resize:none;
            width: 100%;
            padding: 0;
            color: #444;
            font-size: 13px;
            text-indent: 1em;
            border: 1px solid #d0d0d0;
        }
        .userOpenText{
             height: 40px;
        }
        .box_item{
            padding: 10px 0px 0px 0px;
        }
        .userOpenContent{
            height: 100px;
        }
     }
      .butPost{
            width: calc(~"100% - 40px");
            padding: 0px 20px;
            margin: 0px auto;
        }
</style>